.App {
  display: -webkit-box;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  /* height: 100%; */
}

.content {
  flex: 1 1;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}

.footer {
  height: 2.666667rem;
  background: red;
  width: 100%;
}

.item {
  display: -webkit-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

img {
  width: 100%;
  height: auto;
}

.title {
  width: 100%;
  font-size: 0.426667rem;
  display: block;
}
